}
.carousel-indicators {
  width: 600px;
  display: flex;
  margin-right: 300px;
}
.carousel-indicators li {
  display: block;
  border-radius: 50%;
  overflow: hidden;
  width: 80px !important;
  height: 80px !important;
}
.carousel-indicators li img {
  width: 80px;
  height: 80px;
}
.carousel-indicators .active {
